* {
  font-family: Helvetica, Arial, sans-serif;
}
body {
  padding: 60px;
}
input {
  width: 100%;
  padding: 5px;
}
.gridList {
  padding: 0;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin: 0 10px 10px 0;
  width: 220px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 150px;
}

.cardGrid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.cardGrid h3 {
  display: block;
  width: 100%;
}
